/* https://doodlenerd.com/snippet/golden-ratio-with-css-grid-layout/nphSIoIB_OI?view=editor */
  .container2 {
    width: 70vw;
    height: 43.2632880099vw;
    border: 0.5px solid #4A4949;
    display: grid;
    grid-template-columns: 61.8% 9.02% 5.58% 23.6%;
    grid-template-rows: 61.8% 9.02% 5.58% 23.6%;
    grid-template-areas: "A B B B" "A E F C" "A E G C" "A D D C";
  }
  
  .item2 {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    color: #F2F2F2;
    font-size: 1.5em;
    font-weight: 700;
    text-shadow: 1px 1px 1px #878787;
    box-sizing: border-box;
    border: 0.5px solid #4A4949;
  }
  .item2.a {
    grid-area: A;
    background-color: #a0ddff;
  }
  .item2.b {
    grid-area: B;
    background-color: #c1cefe;
  }
  .item2.c {
    grid-area: C;
    background-color: #758ecd;
  }
  .item2.d {
    grid-area: D;
    background-color: #7189ff;
  }
  .item2.e {
    grid-area: E;
    font-size: 1.2em;
    background-color: #624cab;
  }
  .item2.f {
    grid-area: F;
    font-size: 1em;
    background-color: #5d2e8c;
  }
  .item2.g {
    grid-area: G;
    font-size: 0.7em;
    background-color: #7a7a7a;
  }
  